
import { logOut } from '@/service/getData'
import {judgeInv} from '@/config/util.js'
import Vue from 'vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Cookie from 'vux/src/tools/cookie/index.js'

Vue.use(AlertPlugin);

export default {
name: 'myInfor',
components: {
AlertPlugin,InlineLoading
},
data() {
return {
isLogOutCli:false,
isLoading:false,
telphone:this.$store.state.webChatUserInfo.loginPhone,
company:this.$store.state.webChatUserInfo.departmentName,
show:false
}
},
beforeCreate() {

},
created() {

},
methods: {

logOut() {
this.show=true

var that=this;
if (this.isLogOutCli) return;
this.isLogOutCli = true;
this.isLoading=true;
logOut().then(function (res) {

if (res.resultCode === '00') {
debugger
that.$store.state.isLogin=false;
that.$store.state.webChatSatetInfo={};
that.$store.state.webChatUserInfo={};

Cookie.remove("iaaState")
setTimeout(function() {
that.isLogOutCli = false;
that.isLoading=false;
judgeInv();
}, 200)
} else {
that.isLogOutCli = false;
that.isLoading=false;
that.$vux.alert.show({
title: '提示',
content: res.resultMsg || '系统出错，请稍后再试'
});
}
})
.catch(function () {
that.isLogOutCli = false;
that.isLoading=false;
that.$vux.alert.show({
title: '提示',
content: '网络似乎有点问题，请稍后再试'
});
})
}

}
}



